var core = require('./core');
var Queue = require('./queue');
var EpubCFI = require('./epubcfi');
var EventEmitter = require('event-emitter');

/**
 * Find Locations for a Book
 * @param {Spine} spine
 * @param {request} request
 */
function Locations(spine, request) {
	this.spine = spine;
	this.request = request;

	this.q = new Queue(this);
	this.epubcfi = new EpubCFI();

	this._locations = [];
	this.total = 0;

	this.break = 150;

	this._current = 0;

};

/**
 * Load all of sections in the book to generate locations
 * @param  {int} chars how many chars to split on
 * @return {object} locations
 */
Locations.prototype.generate = function(chars) {

	if (chars) {
		this.break = chars;
	}

	this.q.pause();

	this.spine.each(function(section) {

		this.q.enqueue(this.process.bind(this), section);

	}.bind(this));

	return this.q.run().then(function() {
		this.total = this._locations.length-1;

		if (this._currentCfi) {
			this.currentLocation = this._currentCfi;
		}

		return this._locations;
		// console.log(this.percentage(this.book.rendition.location.start), this.percentage(this.book.rendition.location.end));
	}.bind(this));

};

Locations.prototype.createRange = function () {
	return {
		startContainer: undefined,
		startOffset: undefined,
		endContainer: undefined,
		endOffset: undefined
	}
};

Locations.prototype.process = function(section) {

	return section.load(this.request)
		.then(function(contents) {
			var locations = this.parse(contents, section.cfiBase);
			this._locations = this._locations.concat(locations);
		}.bind(this));

};

Locations.prototype.parse = function(contents, cfiBase, chars) {
	var locations = [];
	var range;
	var doc = contents.ownerDocument;
	var body = core.qs(doc, 'body');
	var counter = 0;
	var prev;
	var _break = chars || this.break;
	var parser = function(node) {
		var len = node.length;
		var dist;
		var pos = 0;

		if (node.textContent.trim().length === 0) {
			return false; // continue
		}

		// Start range
		if (counter == 0) {
			range = this.createRange();
			range.startContainer = node;
			range.startOffset = 0;
		}

		dist = _break - counter;

		// Node is smaller than a break,
		// skip over it
		if(dist > len){
			counter += len;
			pos = len;
		}

		while (pos < len) {
			// counter = this.break;
			pos += dist;
			// Gone over
			if(pos >= len){
				// Continue counter for next node
				counter = len - (pos - _break);
			// At End
			} else {
				// End the previous range
				range.endContainer = node;
				range.endOffset = pos;
				// cfi = section.cfiFromRange(range);
				cfi = new EpubCFI(range, cfiBase).toString();
				locations.push(cfi);
				counter = 0;

				// Start new range
				pos += 1;
				range = this.createRange();
				range.startContainer = node;
				range.startOffset = pos;

				dist = _break;
			}
		}
		prev = node;
	};

	core.sprint(body, parser.bind(this));

	// Close remaining
	if (range && range.startContainer && prev) {
		range.endContainer = prev;
		range.endOffset = prev.length;
		// cfi = section.cfiFromRange(range);
		cfi = new EpubCFI(range, cfiBase).toString();
		locations.push(cfi);
		counter = 0;
	}

	return locations;
};

Locations.prototype.locationFromCfi = function(cfi){
	// Check if the location has not been set yet
	if(this._locations.length === 0) {
		return -1;
	}
	return core.locationOf(cfi.start, this._locations, this.epubcfi.compare);
};

Locations.prototype.percentageFromCfi = function(cfi) {
	if(this._locations.length === 0) {
		return null;
	}
	// Find closest cfi
	var loc = this.locationFromCfi(cfi);
	// Get percentage in total
	return this.percentageFromLocation(loc);
};

Locations.prototype.percentageFromLocation = function(loc) {
	if (!loc || !this.total) {
		return 0;
	}
	return (loc / this.total);
};

